import back from '@/components/back'
import comSelect from '@/components/select'

export default {
    components:{
        back,
        comSelect
    },
    data(){
        return{
            a: false,
            b: false,
            c: false,
            d: false,
            e: false,
            f: false,
            num: 1,
            num2: 1,
            usechoose: {
                UsageValue: 0,
                UsageName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.UsageMethod : ''
            },
            sizechoose: {
                UsageValue: 0,
                UsageName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.DayDosage : ''
            },
            timechoose: {
                UsageValue: 0,
                UsageName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.UsageTime : ''
            },
            unitchoose: {
                CategoryItemValue: 0,
                CategoryItemName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.DosageUnit : ''
            },
            numchoose: {
                CategoryItemValue: 0,
                CategoryItemName: this.$store.state.cMed.PackageUnit
            },
            longchoose: '',
            size: [],
            useTime: [],
            useType: [],
            numType: [],
            longType: [],
            info: this.$store.state.cMed,
            Remark: '',
            Unit: [],
            flag: null,
            addNew: null,
            newName: '',
            flag2: false,
			showConfirm:false,
			showTitle:'',
        }
    },
