    created(){
        this.$store.dispatch('pushsaveFlag', 0)
        this.pull()
        const { cMed } = this.$store.state
        this.num = cMed.Dosage || 1
        this.num2 = cMed.Quantity || 1
        this.Remark = cMed.Remark
		//console.log(this.info)
    },
    methods: {
        show(f){
            this.flag = f
            this[f] = true
        },
		operate(type, num){
            if(this.flag2 && num == 'num'){
                return
            }
            if(type === 'del' && this[num] > 1){
                this[num] -= 1
            }
            else if(type === 'add'){
                this[num] = +(this[num]) + 1
            }
        },
        pick(data){
			if(!this.newName && this.addNew == 'numType'){
			    return this.$toast('请输入自定义内容')
			}
            this[this.flag] = false
            if(this.flag == 'a'){
                if(data.CategoryItemValue == 20){
                    this.num = 0
                    this.flag2 = true
                }
                else{
                    this.flag2 = false
                }
                this.unitchoose = data || ''
            }
            if(this.flag == 'b'){
                this.sizechoose = data || ''
            }
            else if(this.flag == 'c'){
                this.timechoose = data || ''
            }
            else if(this.flag == 'd'){
                this.usechoose = data || ''
            }
            else if(this.flag == 'e'){
                this.numchoose = data || ''
            }
            else if(this.flag == 'f'){
                this.longchoose = data || ''
            }
			this.addNew = null
        },
        pull(){
            this.$post('PlatFormAPI/Category/QueryCategoryItem',{
                CategoryIds:[ 12, 38 ]
            }).then(o => {
                let u = 0
                let n = 0
                const { cMed } = this.$store.state
                o.forEach(v => {
                    if(v.CategoryId === 12){
                        this.Unit = v.Items
                        this.Unit.forEach(a => {
                            if(a.CategoryItemValue == cMed.DosageUnitId){
                                this.unitchoose = a
                                u = 1
                            }
                        })
                    }
                    else if(v.CategoryId === 38){
                        this.numType = v.Items
                        this.numType.forEach(a => {
                            if(a.CategoryItemName == cMed.PackageUnit){
                                this.numchoose = a
                                n = 1
                            }
                        })
                    }
                })
                if(u==0){
                    let uArr = {
                        DosageUnitId: 0,
                        CategoryItemValue: 0,
                        CategoryItemName: cMed.DosageUnit
                    }
                    this.Unit.push(uArr)
                    this.unitchoose = uArr
                }
                if(n==0){
                    let nArr = {
                        DosageUnitId: 0,
                        CategoryItemValue: 0,
                        CategoryItemName: cMed.PackageUnit
                    }
                    this.numType.push(nArr)
                    this.numchoose = nArr
                }
            })
            this.$post('Prescription/Medication/GetMedicationUsage').then(o => {
                let s = 0
                let u = 0
                let ut = 0
                let l = 0
                const { cMed } = this.$store.state
                o.Data.forEach(v => {
                    if(v.UsageType === 3){
                        this.size.push(v)
                        this.size.forEach(a => {
                            if(a.UsageValue == cMed.DayDosageId){
                                this.sizechoose = a
                                s = 1
                            }
                        })
                    }
                    else if(v.UsageType === 1){
                        this.useTime.push(v)
                        this.useTime.forEach(a => {
                            if(a.UsageValue == cMed.UsageTimeId){
                                this.timechoose = a
                                u = 1
                            }
                        })
                    }
                    else if(v.UsageType === 2){
                        this.useType.push(v)
                        this.useType.forEach(a => {
                            if(a.UsageValue == cMed.UsageMethodId){
                                this.usechoose = a
                                ut = 1
                            }
                        })
                    }
                    else if(v.UsageType === 4){
                        this.longType.push(v)
                        this.longType.forEach(a => {
                            if(a.UsageValue == cMed.DosageDaysId){
                                this.longchoose = a
                                l = 1
                            }
                        })
                    }
                })
                //console.log(s)
                if(s == 0){
                    console.log(111)
                    let sArr = {
                        UsageValue: cMed.DayDosageId,
                        UsageName: cMed.DayDosage
                    }
                    this.size.push(sArr)
                    this.sizechoose = sArr
                }
                if(u==0 && cMed.UsageTime){
                    let uArr = {
                        UsageValue: cMed.UsageTimeId,
                        UsageName: cMed.UsageTime
                    }
                    this.useTime.push(uArr)
                    this.timechoose = uArr
                }
                if(ut==0){
                    let utArr = {
                        UsageValue: cMed.UsageMethodId,
                        UsageName: cMed.UsageMethod
                    }
                    this.useType.push(utArr)
                    this.usechoose = utArr
                }
                if(l==0 && cMed.DosageDays){
                    let lArr = {
                        UsageValue: cMed.DosageDaysId,
                        UsageName: cMed.DosageDays
                    }
                    this.longType.push(lArr)
                    this.longchoose = lArr
                }

            })
        },
        save(){
            if(!this.usechoose.UsageName){
                return this.$toast('请选择给药途径')
            }
            if(!this.sizechoose.UsageName){
                return this.$toast('请选择每日次数')
            }
            if(!this.num){
                return this.$toast('请选择药品用量')
            }
            if(!this.unitchoose.CategoryItemName){
                return this.$toast('请选择用量单位')
            }
            if(!this.num2){
                return this.$toast('请选择药品数量')
            }
            if(this.num2%1!==0 || this.num2<1){
                return this.$toast('药品数量必须为整数')
            }
            if(!this.numchoose.CategoryItemName){
                return this.$toast('请选择数量单位')
            }
            if(this.num <= 0 && this.unitchoose.CategoryItemName != '适量'){
                return this.$toast('请选择每次用量')
            }
			if(this.info.Stock < this.num2){
				this.showConfirm = true
				this.showTitle = '药品最大库存为'+this.info.Stock
				return
			}

            this.info.UsageTimeId = this.timechoose ? this.timechoose.UsageValue : 0
            this.info.UsageTime = this.timechoose ? this.timechoose.UsageName : null
            this.info.UsageMethodId = this.usechoose.UsageValue
            this.info.UsageMethod = this.usechoose.UsageName
            this.info.DayDosageId = this.sizechoose.UsageValue
            this.info.DayDosage = this.sizechoose.UsageName
            this.info.PackageUnitId = this.numchoose.CategoryItemValue
            this.info.PackageUnit = this.numchoose.CategoryItemName
            this.info.Dosage = this.num
            this.info.Quantity = this.num2
            this.info.Remark = this.Remark
            this.info.DosageUnit = this.unitchoose.CategoryItemName
            this.info.DosageUnitId = this.unitchoose.CategoryItemValue
            this.info.DosageDays = this.longchoose ? this.longchoose.UsageName : null
            this.info.DosageDaysId = this.longchoose ? this.longchoose.UsageValue : 0
            const idx = this.$route.query.idx
            this.$store.dispatch('pushsaveFlag', 1)
            this.$store.dispatch('pushpullTemp', 0)
            this.$store.dispatch('pushpullTemp', 0)
            if(idx){
                const list = this.$store.state.upList
                list.splice(idx, 1, this.info)
                this.$store.dispatch('pushupList', list)
                this.$store.dispatch('pushcMed', null)
            }
            else{
                this.$store.state.upList.push(this.info)
                this.$store.dispatch('pushcMed', this.info)
            }
            this.$router.back()
        },
        back(){
            this.$store.dispatch('pushcMed', null)
            this.$router.back()
        },
        cancel(){
            this.addNew = null
            if(this.addNew == 'size'){
                this.sizechoose = null
            }
            else if(this.addNew == 'Unit'){
                this.unitchoose = null
            }
            else if(this.addNew == 'useTime'){
                this.timechoose = null
            }
            else if(this.addNew == 'useType'){
                this.usechoose = null
            }
            else if(this.addNew == 'numType'){
                this.numchoose = null
            }
            else if(this.addNew == 'longType'){
                this.longchoose = null
            }
        },
        confirm(){
            if(!this.newName){
                return this.$toast('请输入自定义内容')
            }
            if(this.addNew == 'size'){
                this.size.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.sizechoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            else if(this.addNew == 'Unit'){
                this.Unit.push({
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                })
                this.unitchoose = {
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                }
            }
            else if(this.addNew == 'useTime'){
                this.useTime.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.timechoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            else if(this.addNew == 'useType'){
                this.useType.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.usechoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            else if(this.addNew == 'numType'){
                this.numType.push({
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                })
                this.numchoose = {
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                }
            }
            else if(this.addNew == 'longType'){
                this.longType.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.longchoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            this.newName = ''
            this.addNew = null
            this.a = false
            this.b = false
            this.c = false
            this.d = false
            this.e = false
            this.f = false
        }
	},
}



